.form_input {
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 75%;
    margin: 1rem 0;
    outline: none;
    transition: all 0.3s;
  }
  
  .label {
    align-self: flex-start;
    font-size: 20px;
    color: #fff;
    font-weight: bold;
    margin: 0;
    display: block;
  }
  
  .input {
    width: 100%;
    height: 40px;
    border: 1px solid #000;
    border-radius: 5px;
    padding: 5px 10px;
    font-size: 14px;
    color: #333;
    outline: none;
    transition: all 0.3s;
  }
  
  .error {
    align-self: flex-start;
    text-align: left;
    color: red;
    font-size: 16px;
    margin-top: 0.25rem;
    padding: 0;
  }