.sign_in {
    width: 500px;
    max-height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
    background-color: #000;
    border-radius: 0 20% 0 20%;
  }
  .center {
    display: flex;
    height: 100vh;
    width: 100%;
    justify-content: center;
    align-items: center;
    background-color: #2c2c2c;
  }
  .sign_in h1 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
  }
  .signInForm {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  .link {
    color: #fff;
    text-decoration: none;
    font-weight: 600;
  }
  
  @media screen and (max-width: 600px) {
    .sign_in {
      width: 85%;
      border-radius: 10px;
    }
  }
  
  @media screen and (min-height: 767px) {
    .sign_in {
      min-height: 50vh;
    }
  }